
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700");
.l-brands .md-brands__check-brand {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: flex;
  -webkit-box-align: center;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.l-brands .md-brands__check-brand {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -moz-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.l-home-page {
  margin-top: 48px;
}
.l-home-page .md-landing-page .md-landing-page__default {
    width: 100%;
}
@media (min-width: 600px) {
.l-home-page {
      margin-top: 0;
}
}
.l-new-products {
  margin: 0 15px;
}
@media (min-width: 600px) {
.l-new-products {
      margin: 0 30px;
}
}
.md-new-products__title, .l-brands .md-brands__title, .l-current-offers .md-current-offers__title {
  text-transform: uppercase;
  font-size: 16px;
  color: #444A59;
  text-align: center;
  margin: 15px 0;
}
@media (min-width: 600px) {
.md-new-products__title, .l-brands .md-brands__title, .l-current-offers .md-current-offers__title {
      text-align: left;
      font-size: 24px;
      margin: 15px 10px;
}
}
.l-brands {
  margin: 15px 0;
  padding: 15px;
  background-color: #fff;
}
@media (min-width: 600px) {
.l-brands .md-brands__title {
      margin: 15px 0;
}
}
@media (min-width: 600px) {
.l-brands {
      margin: 75px 0;
      padding: 40px 40px 75px;
}
}
.l-brands .md-brands__check-brand {
    text-transform: uppercase;
    border-radius: 5px;
    border: 2px solid #d9d9d9;
    color: #444A59;
    -webkit-transition: .3s ease;
    transition: .3s ease;
    text-decoration: none;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 35px;
    margin-bottom: 35px;
}
.l-brands .md-brands__check-brand:hover {
      background-color: #444A59;
      border-color: #444A59;
      color: #fff;
}
@media (min-width: 600px) {
.l-brands .md-brands__check-brand {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        width: 250px;
}
}
.md-brands__select {
  height: 35px;
  padding: 5px;
  background-color: #444A59;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 4px;
  margin: 15px 0;
}
@media (min-width: 600px) {
.md-brands__select {
      width: 370px;
}
}
.l-current-offers {
  margin: 15px;
}
@media (min-width: 600px) {
.l-current-offers .md-current-offers__title {
      text-align: left;
      font-size: 24px;
      margin: 15px 10px;
}
}
@media (min-width: 600px) {
.l-current-offers {
      margin: 15px 30px 50px;
}
}



