
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700");
.md-product-listing__return-btn, .l-pagination .md-pagination {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: flex;
  -webkit-box-align: center;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.md-product-listing__return-btn, .l-pagination .md-pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -moz-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.l-product-listing-container {
  margin: 83px 15px 40px;
}
@media (min-width: 600px) {
.l-product-listing-container {
      margin: 30px;
}
}
.l-product-listing {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
@media (min-width: 668px) {
.l-product-listing {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
}
}
.md-product-listing__return-btn {
  text-decoration: none;
  color: #444A59;
  background-color: #fff;
  border: 2px solid #444A59;
  border-radius: 5px;
  height: 35px;
  text-transform: uppercase;
  margin-bottom: 30px;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  width: 100%;
}
.md-product-listing__return-btn:hover {
    background-color: #444A59;
    color: #fff;
}
@media (min-width: 600px) {
.md-product-listing__return-btn {
      width: 130px;
      margin: 0 10px 25px;
}
}
.l-pagination .md-pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  height: 45px;
  margin: 40px 0 50px;
  padding: 0;
}
.l-pagination .md-pagination__pages {
  color: #444A59;
  font-size: 13px;
  font-weight: 700;
  cursor: pointer;
  margin: 20px;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}
@media (min-width: 600px) {
.l-pagination .md-pagination__pages {
      font-size: 16px;
}
}
@media (min-width: 1024px) {
.l-pagination .md-pagination__pages {
      font-size: 18px;
}
}



