
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700");
.l-app-header, .l-header-quick-access, .l-header-quick-access .md-mini-bag__quantity {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: flex;
  -webkit-box-align: center;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.l-header-quick-access .md-mini-bag__quantity {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -moz-flexbox;
  display: flex;
