    color: #fff;
    font-weight: 700;
}
@media (min-width: 1360px) {
.l-header-quick-access .md-mini-bag__quantity {
        width: 20px;
        height: 20px;
        font-size: 10px;
}
}
@media (min-width: 600px) {
.l-header-quick-access {
      width: auto;
      margin-top: 0;
}
}



